/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif; }

.container {
  background: #fee82d;
  height: 100%;
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }
  .container .pokedex_search-input {
    margin-top: 5%;
    height: 70px; }
    .container .pokedex_search-input input {
      width: 900px;
      height: 50px;
      font-size: 15px;
      text-align: center; }
  .container .pokedex_cards-container {
    display: flex;
    width: 900px;
    justify-content: center;
    flex-wrap: wrap; }
    .container .pokedex_cards-container .pokedex_card {
      width: 250px;
      height: 350px;
      background: white;
      margin-top: 2%; }
      .container .pokedex_cards-container .pokedex_card:not(last-child) {
        margin-right: 2%; }
      .container .pokedex_cards-container .pokedex_card .pokedex_card-img-container {
        width: 100%;
        height: 150px;
        align-items: center;
        display: flex;
        justify-content: center;
        background: #dfdfdf;
        position: relative; }
        .container .pokedex_cards-container .pokedex_card .pokedex_card-img-container .pokedex_card-img-counter {
          background: #d2cdca;
          position: absolute;
          padding: 3%;
          bottom: 0;
          left: 0; }
      .container .pokedex_cards-container .pokedex_card .pokedex_card-dates-container {
        padding: 10%; }
        .container .pokedex_cards-container .pokedex_card .pokedex_card-dates-container .pokedex_card-dates-title {
          font-size: 25px;
          text-transform: capitalize; }
        .container .pokedex_cards-container .pokedex_card .pokedex_card-dates-container .pokedex_card-pills-container {
          display: flex;
          flex-wrap: wrap;
          margin-top: 2%; }
          .container .pokedex_cards-container .pokedex_card .pokedex_card-dates-container .pokedex_card-pills-container .pokedex_card-pill {
            color: #dfdfdf;
            border: 1px solid #dfdfdf;
            padding: 2%;
            border-radius: 20px;
            text-transform: uppercase;
            font-size: 10px;
            margin-right: 2%;
            margin-top: 2%; }
  .container .none {
    display: none; }
